
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;400;700&display=swap');

*{
    box-sizing: border-box;
    margin: 0;
}

:root{
    --primary-color:#72318e;
    --secondary-color: #9f44c7;
}

body{
    background-color: var(--primary-color);
    font-family: 'Poppins', sans-serif;
    margin: 0;
}


/* =================================header========================================= */
.header__container{    
    max-width: 1300px;
    margin: 0 auto;
    
}

.header__nav {
    background-color: var(--secondary-color);
    margin: 0;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 99;
}

.header__content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 16px;
    height: 80px;
}

.header__logo{
    font-size: 32px;
    font-weight: 700;
    color: #ffffff;
    text-decoration: none;   
}
.search{
    background-color: transparent;
    border: 2px solid var(--primary-color);
    padding:10px 20px;
    border-radius: 50px;
    font-size: 1rem;
    color:#fff;
    font-family: inherit;
    
}

.search:focus{
    outline:0;
    background-color: var(--primary-color);
}

.search::placeholder{
    color: #3e1b4d;
    font-weight:500 ;
    
}


.tags__container{
    max-width: 1300px;
    padding-top: 100px;
}

/* ========================================movie================================= */
main{
    display:flex;
    flex-wrap: wrap;
    justify-content: center;
}

.movie {
    width: 300px;
    margin: 1rem;
    border-radius: 3px;
    box-shadow: 0.2px 4px 5px rgba(0,0,0,0.2);
    background-color: var(--secondary-color);
    position:relative;
    overflow: hidden;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}


.movie img{
    width:100%;
}

.movie-info{
    color:#eee;
    display: flex;
    align-items: center;
    justify-content: center; 
    padding: 10px 15px;
    letter-spacing: 0.5px;
    height: 100px; /* задання фіксованої висоти */
}

.movie-info h3{
    margin-top: 0;
    margin-bottom: 0;
    flex-grow: 1; 
    text-align: left; 
}

.movie-info span{
    width: 50px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #43025f;
    border: 1px solid #ffffff;
    padding: 0.25rem 0.5rem;
    border-radius: 50%;
    font-weight: bold;
    margin-left: 1rem; 
    position: absolute;
    top: 10px;
    right: 10px;
}

.movie-info span.green{
    color:rgb(19, 221, 19);
}

.movie-info span.orange{
    color:rgb(235, 227, 2);
}

.movie-info span.red{
    color:red;
}

.overview{
    position:absolute;
    left:0;
    right:0;
    bottom:0;
    background-color: #fff;
    padding: 1rem;
    max-height: 100%;
    transform:translateY(101%);
    transition:transform 0.3s ease-in;
}

/* .movie:hover .overview{
    transform:translateY(0);
    background-color: #cea9df;
    height: 100%;
    cursor: pointer;
} */

.movie:hover{    
    cursor: pointer;
    transform: scale(1.02);
    box-shadow: -3px 1px 37px -1px rgba(252,238,246,0.75);

}

/* ==============================genresBtns========================================== */
#tags{
    width:80%;
    display:flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    margin: 10px auto;
   
}

.tag{
    color:white;
    padding:10px 20px;
    background-color: orange;
    border-radius: 50px;
    margin:5px;
    display:inline-block;
    cursor: pointer;
}

.tag:hover{
    background-color: rgb(240, 85, 12);
    box-shadow: 0px 0px 22px 0px rgba(250,  244,  244, 0.61);
}

.tag.highlight{
    background-color: rgb(240, 85, 12);    
    box-shadow: 0px 0px 22px 0px rgba(250,  244,  244, 0.61);
}
.no-results{
    color:white;
}

.clear-btn {
    background-color: rgb(25, 0, 255);   
    box-shadow: 0px 0px 22px 0px rgba(250,  244,  244, 0.61);
}

.clear-btn:hover{
    background-color:#231187 ;
}
/* ======================================pagination======================== */

.pagination{
    display:flex;
    margin:10px 30px;
    align-items: center;
    justify-content: center;
    color:white;
}

.page{
    padding:20px;
    cursor:pointer;
}

.page:hover,
.page:focus {
color: orange;
scale: 1.2;
}

.page.disabled{
    cursor:not-allowed;
    color:grey;
}

.current{    
    display: flex;
    justify-content: center;
    align-items: center;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    border: 5px solid orange;
    font-size: 20px;
    font-weight: 600;
    cursor: pointer;
    margin: 5px;
}

.current:hover {
background-color: orange;
}

.know-more{
    background-color: orange;
    color:white;
    font-size: 16px;
    font-weight: bold;
    border:0;
    border-radius: 50px;
    padding: 10px 20px;
    margin-top: 5px;
}
/* The Overlay (background) */
.overlay {
    height: 100%;
    width: 0;
    position: fixed; /* Stay in place */
    z-index: 1; /* Sit on top */
    left: 0;
    top: 0;
    background-color: rgb(0,0,0); /* Black fallback color */
    background-color: rgba(0,0,0, 0.9); /* Black w/opacity */
    overflow-x: hidden; /* Disable horizontal scroll */
    transition: 0.5s; /* 0.5 second transition effect to slide in or slide down the overlay (height or width, depending on reveal) */
  }
  
  /* Position the content inside the overlay */
  .overlay-content {
    position: relative;
    top: 10%; /* 25% from the top */
    width: 100%; /* 100% width */
    text-align: center; /* Centered text/links */
    margin-top: 30px; /* 30px top margin to avoid conflict with the close button on smaller screens */
  }
  
  /* The navigation links inside the overlay */
  .overlay a {
    padding: 8px;
    text-decoration: none;
    font-size: 36px;
    color: #818181;
    display: block; /* Display block instead of inline */
    transition: 0.3s; /* Transition effects on hover (color) */
  }
  
  
  .overlay a:hover, .overlay a:focus {
    color: #f1f1f1;
  }
  
 
  .overlay .closebtn {
    position: absolute;
    top: 20px;
    right: 45px;
    font-size: 60px;
  }
  
 
  @media screen and (max-height: 450px) {
    .overlay a {font-size: 20px}
    .overlay .closebtn {
      font-size: 40px;
      top: 15px;
      right: 35px;
    }
  }


  .embed.hide{
      display: none;
  }

  .embed.show{
      display: inline-block;
  }

  .arrow{
      position: absolute;
      font-size: 40px;
  }

  .arrow.left-arrow{
      top:50%;
      left:5%;
      transform: translateY(-50%);
  }

  
  .arrow.right-arrow{
    top:50%;
    transform: translateY(-50%);
    right:5%;
}

.dots{
    margin-top: 30px;
}

.dots .dot {
    padding: 5px 15px;
    border-radius: 50%;
    border: 5px solid var(--secondary-color);
    color: white;
    font-size: 20px;
    font-weight: 600;
    margin: 5px;
}

.dots .dot.active{
    border-color: orange;
}

/* ========================================upward===================== */
.upward{
    position: fixed;
    bottom: 700px;
    right: 40px;
    width: 50px;
    height: 50px;
    background-color: orange;
    border: 3px solid orangered;
   
    cursor: pointer;
    border-radius: 50%;    
    font-size: 20px; 
    line-height: 70px;
    
    font-weight: 800;
    opacity: 0;
    transition: 0.5s;
    z-index: 999;
}

.active{
    bottom: 40px;
    visibility: visible;
    opacity: 1;
    z-index: 999;
}

.upward:hover{
    background-color: orange;   
    scale: 1.2; 
    /* border: 3px solid #000000;     */
}

.arrow{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    fill: orangered;
}

.dark-mode .upward{
    border: 3px solid #ffffff;
}
.dark-mode .arrow{
    fill:#ffffff
}
